<template>
	<div class="row">
		<button @click.stop.prevent="create" class="btn btn-success pull-right">
			<i class="fa fa-plus pull-left"></i>
			Nueva venta
		</button>
		<div class="card" v-if="!currentItem">
			<div class="card-content">
				<v-client-table ref="tabla" class="table-responsive" :data="sales" :columns="columns" :options="options">
					<template slot="actions" scope="props" class="td-actions">
						<div style="white-space:nowrap">
							<button type="button" data-toggle="tooltip" @click.stop.prevent="show(props.row)" class="btn btn-xs btn-info">
								<i class="material-icons">remove_red_eye</i>
							</button>
							<button type="button" @click.stop.prevent="edit(props.row)" class="btn btn-xs btn-success">
								<i class="material-icons">edit</i>
							</button>
							<button type="button" @click.stop.prevent="remove(props.row)" class="btn btn-xs btn-danger">
								<i class="material-icons">close</i>
							</button>
						</div>
					</template>
				</v-client-table>	
			</div>
		</div>
		<div class="card" v-else-if="currentItem">
			<div class="card-content">
				<div class="col-xs-12">
					<button class="btn btn-xs btn-success pull-right"
					@click.stop.prevent="edit(currentItem)"
					v-if="currentItem && !editing">
					<i class="material-icons">edit</i> Editar
				</button>
				<button class="btn btn-xs btn-warning pull-right" @click.stop.prevent="back">
					<i class="material-icons">chevron_left</i> Atrás
				</button>
			</div>

			<form action="#" @submit.stop.prevent="submit">
				<div class="col-xs-12">
				<div class="form-group">
					<el-select class="form-control" @change="addProduct($event)" v-model="addingProduct" filterable placeholder="Añade un producto" v-if="editing" no-match-text="No encontramos el producto :(">
						<el-option
						v-for="item in products"
						:key="item['.key']"
						:label="item.title"
						:value="item">
						{{ item.title }}
						<h5 class="pull-right" style="margin:0">
							<span class="label" :class="{'label-warning': item.qty <= 5 && item.qty > 0, 'label-danger': item.qty <= 0, 'label-info': item.qty > 5}" v-text="'Stock: '+item.qty"></span>
							<span class="label label-success" v-text="'Precio: '+currency.symbol+' '+item.price"></span>
						</h5>
					</el-option>
				</el-select>
				<div class="card">
					<div class="card-content table-responsive">
						<table class="table">
							<tbody>
								<tr>
									<td>Producto</td>
									<td v-for="(tax, index) in taxes">
										<span class="text-uppercase" :title="tax.operator == '%' ? tax.value+tax.operator : '+'+tax.value" v-text="index"></span>
									</td>
									<td>Cantidad</td>
									<td>Al Por Mayor</td>
									<td>Valor</td>
									<td v-if="editing"></td>
								</tr>
								<tr v-for="(item, key) in currentItem.products" :key="key">
									<td v-text="item.title"></td>
									<td v-for="(taxData, tax) in taxes" class="form-group text-center">
										<div v-if="tax == 'iva'">
											<div v-if="!isNaN(parseInt(taxes.iva.value))">
												<input type="number" v-model.number="item.vat" class="form-control" v-if="editing">
											</div>
											<div v-else>
												<select name="vat" id="vat" class="form-control" v-model="item.vat" v-if="editing">
													<option v-for="(val, key) in taxes.iva.value" :key="key" :value="val" v-text="val+'%'" v-if="key != '.key'"></option>
												</select>
											</div>
												<span v-text="item.vat ? item.vat+'%' : 0+'%'" v-if="!editing"></span>
										</div>
										<div class="checkbox" v-else>
											<label>
												<input type="checkbox" v-model="item.taxes[tax]" checked="checked">
												<span class="checkbox-material"><span class="check"></span></span>
											</label>
										</div>
									</td>
									<td class="form-group text-center">
										<input type="number" v-model.number="item.qty" :name="key+'_cantidad'" data-vv-as="'cantidad'" v-validate="'required|max_value:'+item.stock+'|min_value:1'" class="form-control" v-if="editing">
										<span v-text="item.qty" v-else></span>
										<small class="text-danger" v-if="errors.has(key+'_cantidad')" v-text="errors.first(key+'_cantidad')"></small>
									</td>
									<td class="text-center">
										<div class="checkbox">
											<label>
												<input type="checkbox" v-model="item.useBulkPrice" :disabled="!editing">
												<span class="checkbox-material"><span class="check"></span></span>
											</label>
										</div>
									</td>
									<td class="text-center">
										<span class="price" v-text="currency.symbol+' '+item.finalPrice"></span>
									</td>
									<td class="td-actions text-right" v-if="editing">
										<button type="button" @click.prevent="removeProduct(key)" rel="tooltip" title="" class="btn btn-danger btn-simple btn-xs">
											<i class="material-icons">close</i>
										</button>
									</td>
								</tr>
							</tbody>
						</table>
					</div>
					<div class="panel-footer clearfix">
						<h3 class="pull-left form-group label-floating">
							<label for="discount" class="control-label">Descuento</label>
							<input type="number" id="discount" v-model.number="currentItem.discount" v-validate="'min_value:0'" name="descuento" class="form-control" :disabled="!editing">
							<small class="text-danger" v-if="errors.has('descuento')" v-text="errors.first('descuento')" style="font-size:12px"></small>
						</h3>
						<h3 class="pull-right">Total: <strong v-text="currency.symbol+' '+currentItem.total"></strong></h3>
					</div>
				</div>
			</div>
		</div>
		<div class="col-xs-8 col-xs-offset-2 col-sm-4 col-sm-offset-4" v-if="editing">
			<button class="btn btn-block btn-warning" :disabled="!Object.keys(currentItem.products).length || errors.any()">
				Guardar
			</button>
		</div>
	</form>
</div>
</div>
</div>
</template>

<script>
import Vue from 'vue';
import { ClientTable } from 'vue-tables-2';
import { resourceMixin } from '../helpers.js';
import { Select, Option, Notification } from 'element-ui';

Vue.use(ClientTable, {}, false, null);

export default {
	mixins: [resourceMixin],	
	data () {
        return {
        	resource: 'SALES',
        	columns: ['date', 'discount', 'total', 'actions'],
	        options: {
	        	skin: 'table-striped table-hover',
	        	texts:{
					count: 'Mostrando {from} a {to} de {count} resultados|{count} resultados|1 resultado',
					filter: '',
					filterPlaceholder: 'Filtrar resultados',
					limit:'Resultados:',
					noResults: 'No se encontraron resultados',
					page: 'Página:', // for dropdown pagination 
					filterBy: 'Filtrar por {column}', // Placeholder for search fields when filtering by column
					loading: 'Cargando...', // First request to server
					defaultOption: 'Seleccionar {column}' // default option for list filters
				},
				uniqueKey: '.key',
		        filterable: ['date'],
		        sortable: ['date'],
				headings: {
					date: 'Fecha',
					sub_total: 'Sub Total',
					taxes_applied: 'Impuestos aplicados',
					total: 'Total',
					products: 'Productos',
					discount: 'Descuento',
					actions: 'Acciones'
				},
				orderBy: {
					column: 'date',
					ascending: false
				}
	        },
	        addingProduct: null,
	        watchers: []
        };
	},
	computed: {
		sales () {
			return this.$root.SALES;
		},
		products () {
			return this.$root.PRODUCTS;
		}
	},
	methods: {
		back () {
			for (var i = this.watchers.length - 1; i >= 0; i--) {
				this.watchers[i]();
			}

			this.currentItem = null;
		},
		create () {
			this.editing = true;
			this.currentItem = {
				products: {},
				total: 0,
				discount: 0,
				date: this.$root.firebaseTimestamp()
			};
			this.setupWatchers();
		},
		edit (item) {
			// todo pass it by reference using firebase key
			if (!this.currentItem) {
				this.currentItem = item;
			}
			this.editing = true;
			this.setupWatchers();
		},
		addProduct (product, index) {
			if (this.addingProduct == null) {
				return;
			} else if (!product.qty) {
				Notification.error({
					title: 'Error',
					message: 'El producto se encuentra fuera de stock',
		        });
		        this.addingProduct = null;
		        return;
			}

			var key = product['.key'];
			var item = {
				title: product.title,
				price: product.price,
				bulk_price: product.bulk_price,
				useBulkPrice: false,
				qty: 1,
				stock: product.qty,
				vat: isNaN(parseInt(product.vat)) ? 0 : product.vat
			};

			this.$set(this.currentItem.products, key, item);

			this.addingProduct = null;
		},
		pricedProduct (product) {
			var price = product.price;
			// Determine 
			if (product.useBulkPrice) {
				price = product.bulk_price;
			}

			// apply VAT tax
			price += (product.vat * product.price) / 100;

			product.finalPrice = price * product.qty;
			if (this.currency.roundDecimals) {
				product.finalPrice = Math.round(product.finalPrice.toFixed(this.currency.decimals));
			}
			return product;
		},
		removeProduct (key) {
			this.$delete(this.currentItem.products, key);
		},
		beforeStore () {
			this.currentItem.date = new Date().toLocaleString();
		},
		afterStore () {
			// update stock
			for (var key in this.currentItem.products) {
				var stock = this.currentItem.products[key].stock - this.currentItem.products[key].qty;
				this.$firebaseRefs['PRODUCTS'].child(key).update({qty: stock});
			}
		},
		setupWatchers () {
			this.watchers[this.watchers.length] = this.$watch('currentItem.products', function (newList, oldList) {
				var total = 0;

				// product.finalPrice calculation
				for (var key in newList) {
					newList[key] = this.pricedProduct(newList[key]);
					total += newList[key].finalPrice;

					// also check we are not selling more than stock
					//if (newList[key].qty > newList[key].stock) {
					//	newList[key].qty = newList[key].stock;
					//	Notification.warning({
					//		message: 'Se ha alcanzado la máxima cantidad en stock.',
				    //    });
					//} else if (newList[key].qty < 0) {
					//	newList[key].qty = 0;
					//}
				}

				this.currentItem.total = total - this.currentItem.discount;
			}, {deep: true});

			this.watchers[this.watchers.length] = this.$watch('currentItem.discount', function (newVal, oldVal) {
				var discount = (oldVal ? oldVal : 0) - (newVal ? newVal : 0);
				this.currentItem.total += this.currentItem.total > 0 ? discount : 0;
			});
		}
	},
	components: {
		'el-select': Select,
		'el-option': Option
	},
	mounted () {
		// var vm = this;
	    // this.$refs.tabla.$on('row-click', function () {
		// 	vm.toggleActionsModal();
	    // });
	}
}
</script>

<style src="element-ui/lib/theme-default/icon.css"></style>
<style src="element-ui/lib/theme-default/notification.css"></style>
<style src="element-ui/lib/theme-default/message-box.css"></style>
<style src="element-ui/lib/theme-default/select.css"></style>


