<template>
	<div class="row">
	<button @click.stop.prevent="create" class="btn btn-success pull-right">
		<i class="fa fa-plus pull-left"></i>
		Nuevo proveedor
	</button>
		<div class="card" v-if="!currentItem">
			<div class="card-content">
				<v-client-table ref="tabla" class="table-responsive" :data="providers" :columns="columns" :options="options">
					<template slot="title" scope="props">
						<strong data-toggle="tooltip" :title="props.row.title" v-text="props.row.title.length >= 50 ? props.row.title.substr(0, 47)+'...' : props.row.title">
						</strong>
					</template>
					<template slot="note" scope="props">
						<i data-toggle="tooltip" :title="props.row.note" v-text="props.row.note.length >= 50 ? props.row.note.substr(0, 47)+'...' : props.row.note">
						</i>
					</template>
					<template slot="tel" scope="props">
						<a :href="'tel:'+props.row.tel" v-text="props.row.tel"></a>
					</template>
					<template slot="email" scope="props">
						<a :href="'mailto:'+props.row.email" v-text="props.row.email"></a>
					</template>
					<template slot="actions" scope="props">
						<div style="white-space:nowrap">
							<button type="button" rel="tooltip" data-toggle="tooltip" @click.stop.prevent="show(props.row)" class="btn btn-xs btn-info" data-original-title="xa" title="xa">
	                        	<i class="material-icons">remove_red_eye</i>
	                    	</button>
							<button type="button" @click.stop.prevent="edit(props.row)" rel="tooltip" class="btn btn-xs btn-success" data-original-title="" title="">
	                            <i class="material-icons">edit</i>
	                        </button>
							<button type="button" @click.stop.prevent="remove(props.row)" rel="tooltip" class="btn btn-xs btn-danger" data-original-title="" title="">
	                            <i class="material-icons">close</i>
	                        </button>
						</div>
			    	</template>
				</v-client-table>	
			</div>
		</div>
		<div class="card" v-else-if="currentItem">
			<div class="card-content">
				<div class="col-xs-12">
					<button class="btn btn-xs btn-success pull-right"
						@click.stop.prevent="editing = true"
						v-if="currentItem && !editing">
						<i class="material-icons">edit</i> Editar
					</button>
					<button class="btn btn-xs btn-warning pull-right" @click.stop.prevent="currentItem = null">
						<i class="material-icons">chevron_left</i> Atrás
					</button>
				</div>

				<form action="#" @submit.stop.prevent="submit">
				<div class="row">
					<div class="col-xs-12 col-sm-6">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.title, 'has-error': errors.has('titulo')}">
							<label class="control-label" for="title">Título</label>
							<input type="text" id="title" v-model="currentItem.title" :name="'titulo'" v-validate="'required'" class="form-control" v-if="editing">
							<span v-text="currentItem.title" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('titulo')" v-text="errors.first('titulo')"></small>
						</div>
					</div>
					<div class="col-xs-12 col-sm-6 col-md-4">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.tel, 'has-error': errors.has('tel')}">
							<label class="control-label" for="tel">Teléfono</label>
							<input type="text" id="tel" v-model.number="currentItem.tel" :name="'tel'" v-validate="'required|numeric'" data-vv-as="teléfono" class="form-control" v-if="editing">
							<span v-text="currentItem.tel" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('tel')" v-text="errors.first('tel')"></small>
						</div>
					</div>
				</div>
				<div class="row">
					<div class="col-xs-12 col-sm-6 col-md-4">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.address, 'has-error': errors.has('address')}">
							<label class="control-label" for="address">Dirección</label>
							<input type="text" id="address" v-model="currentItem.address" :name="'address'" v-validate="'required'" data-vv-as="dirección" class="form-control" v-if="editing">
							<span v-text="currentItem.address" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('address')" v-text="errors.first('address')"></small>
						</div>
					</div>
					<div class="col-xs-12 col-sm-6 col-md-4">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.email, 'has-error': errors.has('email')}">
							<label class="control-label" for="email">Correo</label>
							<input type="email" id="email" v-model="currentItem.email" :name="'email'" v-validate="'required'" data-vv-as="correo" class="form-control" v-if="editing">
							<span v-text="currentItem.email" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('email')" v-text="errors.first('email')"></small>
						</div>
					</div>
				</div>
				<div class="row">
					<div class="col-xs-12">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.note, 'has-error': errors.has('note')}">
							<label class="control-label" for="note">Notas</label>
							<textarea cols="30" rows="5" id="note" v-model="currentItem.note" :name="'note'" v-validate="'required'" data-vv-as="notas" class="form-control" v-if="editing"></textarea>
							<span v-text="currentItem.note" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('note')" v-text="errors.first('note')"></small>
						</div>
					</div>
				</div>
					<div class="col-xs-8 col-xs-offset-2 col-sm-4 col-sm-offset-4" v-if="editing">
						<button class="btn btn-block btn-warning" :disabled="errors.any()">
							Guardar
						</button>
					</div>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
import Vue from 'vue';
import { ClientTable } from 'vue-tables-2';
import { resourceMixin } from '../helpers.js';

Vue.use(ClientTable, {}, false, null);

export default {
	mixins: [resourceMixin],	
	data () {
        return {
        	resource: 'PROVIDERS',
        	columns: ['title', 'tel', 'address', 'email', 'note', 'actions'],
	        options: {
		        filterable: ['title'],
		        sortable: ['title'],
				headings: {
					title: 'Título',
					tel: 'Teléfono',
					address: 'Dirección',
					email: 'Correo',
					note: 'Nota',
					actions: 'Acciones'
				}
	        }
        };
	},
	computed: {
		providers () {
			return this.$root.PROVIDERS;
		}
	}
}
</script>

<style src="element-ui/lib/theme-default/icon.css"></style>
<style src="element-ui/lib/theme-default/notification.css"></style>
<style src="element-ui/lib/theme-default/message-box.css"></style>


