<template>
  <div class="sidebar" data-color="orange" data-image="/static/img/sidebar-1.jpg">

      <!--
            Tip 1: You can change the color of the sidebar using: data-color="purple | blue | green | orange | red"

            Tip 2: you can also add an image using data-image tag
        -->

      <div class="logo">
        <a href="/" class="simple-text">
          <img src="/static/img/logo.png" alt="Logo" class="img-responsive center-block" style="max-width:230px">
        </a>
      </div>

        <div class="sidebar-wrapper" ss-container>
              <ul class="nav">
                <router-link v-for="link in links"
                  :key="link.route"
                  :to="link.route"
                  :tag="'li'"
                  :class="{'active': $route.path == link.route}"
                  exact>
                    <a>
                      <i class="material-icons" v-text="link.icon"></i>
                      <p v-text="link.name" class="text-capitalize"></p>
                    </a>
                </router-link>
              </ul>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      links: [
        {
          route: '/',
          icon: 'dashboard',
          name: 'inicio'
        },
        {
          route: '/products',
          icon: 'card_travel',
          name: 'productos'
        },
        {
          route: '/sales',
          icon: 'shopping_cart',
          name: 'ventas'
        },
        {
          route: '/departments',
          icon: 'library_books',
          name: 'departamentos'
        },
        {
          route: '/providers',
          icon: 'local_shipping',
          name: 'proveedores'
        }
      ]
    }
  },
  mounted () {
  }
}
</script>


