<template>
	<div class="row">
	<button @click.stop.prevent="create" class="btn btn-success pull-right">
		<i class="fa fa-plus pull-left"></i>
		Nuevo producto
	</button>
		<div class="card" v-if="!currentItem">
			<div class="card-content">
				<v-client-table ref="tabla" class="table-responsive" :data="products" :columns="columns" :options="options">
					<template slot="title" scope="props">
						<strong data-toggle="tooltip" :title="props.row.title" v-text="props.row.title.length >= 30 ? props.row.title.substr(0, 27)+'...' : props.row.title">
						</strong>
					</template>
					<template slot="department" scope="props">
						{{ props.row.department ? props.row.department : 'n/d' }}
					</template>
					<template slot="provider" scope="props">
						{{ props.row.provider ? props.row.provider : 'n/d' }}
					</template>
					<template slot="actions" scope="props" :class="'td-actions'">
						<div style="white-space:nowrap">
							<button type="button" rel="tooltip" @click.stop.prevent="show(props.row)" class="btn btn-xs btn-info" title="Ver">
	                        	<i class="material-icons row">remove_red_eye</i>
	                    	</button>
							<button type="button" @click.stop.prevent="edit(props.row)" class="btn btn-xs btn-success" title="Editar">
	                            <i class="material-icons row">edit</i>
	                        </button>
							<button type="button" @click.stop.prevent="remove(props.row)" class="btn btn-xs btn-danger" title="Borrar">
	                            <i class="material-icons row">close</i>
	                        </button>
						</div>
			    	</template>
				</v-client-table>	
			</div>
		</div>
		<div class="card" v-else-if="currentItem">
			<div class="card-content">
				<div class="col-xs-12">
					<button class="btn btn-xs btn-success pull-right"
						@click.stop.prevent="editing = true"
						v-if="currentItem && !editing">
						<i class="material-icons">edit</i> Editar
					</button>
					<button class="btn btn-xs btn-warning pull-right" @click.stop.prevent="currentItem = null">
						<i class="material-icons">chevron_left</i> Atrás
					</button>
				</div>

				<form action="#" @submit.stop.prevent="submit">
					<div class="row">
					<div class="col-xs-12 col-sm-6">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.title, 'has-error': errors.has('titulo')}">
							<label class="control-label" for="title">Título</label>
							<input type="text" id="title" v-model="currentItem.title" :name="'titulo'" v-validate="'required'" class="form-control" v-if="editing">
							<span v-text="currentItem.title" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('titulo')" v-text="errors.first('titulo')"></small>
						</div>
					</div>
					<div class="col-xs-12 col-sm-3">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.qty && currentItem.qty != 0, 'has-error': errors.has('cantidad')}">
							<label class="control-label" for="qty">Cantidad</label>
							<input type="number" id="qty" v-model.number="currentItem.qty" :name="'cantidad'" v-validate="'required|min_value:0'" class="form-control" v-if="editing">
							<span v-text="currentItem.qty" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('cantidad')" v-text="errors.first('cantidad')"></small>
						</div>
					</div>
					<div class="col-xs-12 col-sm-3">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.department, 'has-error': errors.has('departamento')}">
							<label class="control-label" for="department">Departamento</label>
							<select id="department" class="form-control" v-model="currentItem.department" :name="'departamento'" v-validate="'required'" v-if="editing">
								<option v-for="(dpt, key) in departments" :key="key" :value="dpt.title" v-text="dpt.title" v-if="key != '.key' && !isEmptyResource(departments)"></option>
								<option v-if="isEmptyResource(departments)" disabled="disabled">No hay ningún departamento registrado</option>
							</select>
							<span v-text="currentItem.department ? currentItem.department : 'n/d'" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('departamento')" v-text="errors.first('departamento')"></small>
						</div>
					</div>
					</div>
					<div class="row">
					<div class="col-xs-12 col-xs-4">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.cost, 'has-error': errors.has('costo')}">
							<label class="control-label" for="cost">Costo</label>
							<input type="number" id="cost" v-model.number="currentItem.cost" :name="'costo'" v-validate="'required|min_value:0'" class="form-control" v-if="editing">
							<span v-text="currentItem.cost" v-else></span>
							<small class="help-block">Costo de producción/adquisición del producto</small>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('costo')" v-text="errors.first('costo')"></small>
						</div>
					</div>
					<div class="col-xs-12 col-xs-4">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.price, 'has-error': errors.has('precio')}">
							<label class="control-label" for="price">Precio</label>
							<input type="number" id="price" v-model.number="currentItem.price" :name="'precio'" v-validate="'required|min_value:1'" class="form-control" v-if="editing">
							<span v-text="currentItem.price" v-else></span>
							<small class="help-block">Precio al público <strong>(sin I.V.A.)</strong></small>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('precio')" v-text="errors.first('precio')"></small>
						</div>
					</div>
					<div class="col-xs-12 col-xs-4">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.bulk_price, 'has-error': errors.has('precioPorMayor')}">
							<label class="control-label" for="bulk_price">Precio por mayor</label>
							<input type="number" id="bulk_price" v-model.number="currentItem.bulk_price" :name="'precioPorMayor'" data-vv-as="precio mayorista" v-validate="'required|min_value:1'" class="form-control" v-if="editing">
							<span v-text="currentItem.bulk_price" v-else></span>
							<small class="help-block">Precio de venta al por mayor</small>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('precioPorMayor')" v-text="errors.first('precioPorMayor')"></small>
						</div>
					</div>
					</div>
					<div class="col-xs-12 col-sm-6">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.provider, 'has-error': errors.has('proveedor')}">
							<label class="control-label" for="provider">Proveedor</label>
							<select id="provider" class="form-control" v-model="currentItem.provider" :name="'proveedor'" v-validate="'required'" v-if="editing">
								<option v-for="(prv, key) in providers" :key="key" :value="prv.title" v-text="prv.title" v-if="key != '.key' && !isEmptyResource(providers)"></option>
								<option v-if="isEmptyResource(providers)" disabled="disabled">No hay ningún proveedor registrado</option>
							</select>
							<span v-text="currentItem.provider ? currentItem.provider : 'n/d'" v-else></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('proveedor')" v-text="errors.first('proveedor')"></small>
						</div>
					</div>
					<div class="col-xs-12 col-sm-6">
						<div class="form-group label-floating" :class="{'is-empty': !currentItem.vat && currentItem.vat !== 0 && editing, 'has-error': errors.has('IVA')}">
							<label class="control-label" for="vat">IVA</label>
							<div v-if="!isNaN(parseInt(taxes.iva.value))">
								<input type="number" v-model.number="currentItem.vat" class="form-control" v-if="editing">
							</div>
							<div v-else>
								<select id="vat" class="form-control" v-model="currentItem.vat" :name="'IVA'" v-validate="'required|min_value:0'" v-if="editing">
									<option v-for="(val, key) in taxes.iva.value" :key="key" :value="val" v-text="val+'%'" v-if="key != '.key'"></option>
								</select>
							</div>
								<span v-text="currentItem.vat ? currentItem.vat+'%' : 0+'%'" v-if="!editing"></span>
							<span class="material-icons form-control-feedback">clear</span>
							<small class="text-danger" v-if="errors.has('IVA')" v-text="errors.first('IVA')"></small>
						</div>
					</div>
					<div class="col-xs-12">
						<div class="form-group">
							<label class="control-label" for="bulk_price">Imágenes</label> <br>
							<small><i>Próximamente... En serio</i></small>
						</div>
					</div>
					<div class="col-xs-8 col-xs-offset-2 col-sm-4 col-sm-offset-4" v-if="editing">
						<button class="btn btn-block btn-warning" :disabled="errors.any()">
							Guardar
						</button>
					</div>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
import Vue from 'vue';
import { ClientTable } from 'vue-tables-2';
import { resourceMixin } from '../helpers.js';
//import { PaginationEvent } from 'vue-pagination-2';

Vue.use(ClientTable, {}, false, null);

export default {
	mixins: [resourceMixin],
	data () {
        return {
			resource: 'PRODUCTS',
        	columns: ['title','qty','cost','price','bulk_price','provider','department','vat','actions'],
	        options: {
				templates: {
		            price: function(h, row) {
		                return h('label', {class:'label label-success'}, ['$ '+row.price]);
		            },
		            bulk_price (h, row) {
		                return h('label', {class:'label label-info'}, ['$ '+row.bulk_price]);
		            },
		            vat (h, row) {
		                return row.vat ? row.vat+'%' : '0%';
		            }
		        },
		        filterable: ['title','qty','provider','department'], // BUG provider & department won't work because we use the keys, not labels
		        sortable: ['title','qty'],
				headings: {
					title: 'Título',
					qty: 'Cantidad',
					cost: 'Costo',
					price: 'Precio',
					bulk_price: 'P. mayor',
					provider: 'Proveedor',
					department: 'Departamento',
					vat: 'I.V.A.',
					actions: 'Acciones'
				}
	        }
        };
	},
	computed: {
		products () {
			return this.$root.PRODUCTS;
		},
		departments () {
			return this.$root.DEPARTMENTSOBJ;
		},
		providers () {
			return this.$root.PROVIDERSOBJ;
		}
	},
	mounted () {
	    //var tabla = this.$refs.tabla;
	    //PaginationEvent.$on('vue-pagination::'+tabla.id, function(e) {});
	}
}
</script>

<style src="element-ui/lib/theme-default/icon.css"></style>
<style src="element-ui/lib/theme-default/notification.css"></style>
<style src="element-ui/lib/theme-default/message-box.css"></style>


