// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import store from './store'
import App from './App';
import config from './config'
import router from './router';
import filters from './filters';
import VueFire from 'vuefire';
import Firebase from 'firebase';
import { Authenticate, AuthMethods } from './Authentication';
import es from 'vee-validate/dist/locale/es';
import VeeValidate, { Validator } from 'vee-validate';

// Add locale helper.
Validator.addLocale(es);

Vue.config.productionTip = false;

Vue.use(VueFire);
Vue.use(VeeValidate, {
  locale: 'es'
});

// filters
for (var name in filters) {
  Vue.filter(name, filters[name]);
}

// directive
//Object.keys(directives).forEach((name) => {
//  Vue.directive(name, directives[name]);
//})

const firebaseApp = Firebase.initializeApp(config.firebase)
const db = firebaseApp.database();

Vue.mixin(AuthMethods);

Authenticate().then((status) => {
  // eslint-disable no-new 
  new Vue({
    el: '#app',
    data: {},
    firebase: {
      CONFIG: {
        source: db.ref('config'),
        asObject: true,
        readyCallback: function () {
          //this.$firebaseRefs.CONFIG.off();
        }
      },
      PRODUCTS: db.ref('products'), // NOTETOSELF Move this to the store state and filter qty > 0
      DEPARTMENTS: db.ref('departments'),
      DEPARTMENTSOBJ: {
        source: db.ref('departments'),
        asObject: true
      },
      PROVIDERS: db.ref('providers'),
      PROVIDERSOBJ: {
        source: db.ref('providers'),
        asObject: true
      },
      SALES: db.ref('sales')
    },
    router,
    store,
    template: '<App/>',
    components: { App },
    mounted () {
      document.getElementById('loader').remove();
      document.getElementById('loader-style').remove();
    }
  })
});


