import { MessageBox, Notification } from 'element-ui';

export function TableOptions() {
	return {

	};
}

export var resourceMixin = {
	data () {
		return {
			editing: false,
			currentItem: null,
			options: {				
	        	skin: 'table-striped table-hover',
	        	texts:{
					count: 'Mostrando {from} a {to} de {count} resultados|{count} resultados|1 resultado',
					filter: '',
					filterPlaceholder: 'Filtrar resultados',
					limit:'Resultados:',
					noResults: 'No se encontraron resultados',
					page: 'Página:', // for dropdown pagination 
					filterBy: 'Filtrar por {column}', // Placeholder for search fields when filtering by column
					loading: 'Cargando...', // First request to server
					defaultOption: 'Seleccionar {column}' // default option for list filters
				},
				uniqueKey: '.key'
			}
		};
	},
	computed: {
		$firebaseRefs () {
			return this.$root.$firebaseRefs;
		},
		config () {
			return this.$root.CONFIG;
		},
		taxes () {
			return this.config.taxes;
		},
		currency () {
			return this.config.currency;
		}
	},
	methods: {
		show (item) {
			this.editing = false;
			this.currentItem = item;
		},
		edit (item) {
			// todo pass it by reference using firebase key
			if (!this.currentItem) {
				this.currentItem = item;
			}
			this.editing = true;
		},
		update () {
			var vm = this,
			key = vm.currentItem['.key'];
			delete vm.currentItem['.key'];
			
			vm.$validator.validateAll().then(() => {
				vm.$firebaseRefs[vm.resource].child(key).update(vm.currentItem)
				.then(function () {
					vm.editing = false;
					vm.currentItem['.key'] = key;
					Notification.success({
			          title: 'Listo!',
			          message: 'Datos actualizados exitosamente'
			        });
				})
				.catch();
			}).catch(() => {				
				Notification.error({
		          title: 'Datos inválidos',
		          message: 'Puede que hayan datos incorrectos.'
		        });
			});
		},
		create () {
			this.editing = true;
			this.currentItem = {};
		},
		store () {
			var vm = this;
			vm.beforeStore();
			vm.$validator.validateAll().then(() => {
				vm.$firebaseRefs[vm.resource].push(vm.currentItem)
				.then(function () {
					var counter = vm.config.counters[vm.resource.toLowerCase()];

					vm.$firebaseRefs.CONFIG.child('counters/'+vm.resource.toLowerCase()).set(!counter && counter !== 0 ? 1 : counter + 1);
					vm.afterStore();

					vm.editing = false;
					Notification.success({
			          title: 'Listo!',
			          message: 'Entrada guardada exitosamente'
			        });
				})
				.catch(function (err) {
					console.error(err);
				});
			}).catch(() => {				
				Notification.error({
		          title: 'Datos inválidos',
		          message: 'Puede que hayan datos incorrectos.'
		        });
			});
		},
		beforeStore () {}, // return not null if we need to stop the saving method
		afterStore () {},
		remove (item) {
			var vm = this;
			MessageBox.confirm('Estás a punto de borrar esta entrada permanentemente', 'Warning', {
		      confirmButtonText: 'Borrar',
		      cancelButtonText: 'Cancelar',
		      confirmButtonClass: 'btn btn-danger',
		      type: 'warning',
		      title: 'Borrar',
		      callback: function (action) {
		      	if (action === 'confirm') {
					var key = item['.key'];
					delete item['.key'];

					vm.$firebaseRefs[vm.resource].child(key).remove()
					.then(function () {
						var counter = vm.config.counters[vm.resource.toLowerCase()];
						counter = counter - 1 < 0 ? 0 : counter - 1; // just to be sure

						vm.$firebaseRefs.CONFIG.child('counters/'+vm.resource.toLowerCase()).set(counter);

						Notification.success({
				          title: 'Hecho',
				          message: 'La entrada ha sido borrada'
				        });
					})
					.catch(function () {
						Notification.error({
				          title: 'Fallo',
				          message: 'No se pudo borrar la entrada, refresque la página e intente nuevamente'
				        });
					});
		      	}
		      }
		    });        
		},
		submit () {
			if (this.currentItem['.key']) {
				this.update();
			}
			else {
				this.store();
			}
		},
		firebalize (dataProp) { // UNUSED
			if (!this[dataProp]) {
				var array = this[dataProp] = [];
				for (var i = 0; i < this.departments.length; i++) {
					array[this.departments[i]['.key']] = this.departments[i];
				}
				return this[dataProp] = array;
			}
			return this[dataProp];
		},
		isEmptyResource (resourceObj) {
			// if firebase sets the `.value` property its because we got no childs
			return resourceObj.hasOwnProperty('.value');
		}
	}
}


